<template>
  <div id="twoupdata">
    <Music />
    <div class="home_container">
      <div class="iconback" @click="back">
        <img src="./../../assets/back.png" alt="" />
      </div>
      <div class="home_title">
        <div>雀巢咖啡，提醒每一天</div>
        <h5>同学，请放下手机专心读书吧！</h5>
      </div>
      <div class="home_info">
        <div class="home_info_cont">
          <p>已兑换咖啡：</p>
