        </div>
        <div v-if="userInfo.beans == 3" class="hecheng" @click="hcbtn">
          合成
        </div>
      </div>
      <div class="home_tishi">
          <p>注:计时开始后，请不要随意退出该页面，每满60分钟才可成功记录</p>
      </div>
      <div class="home_ing">
        <img src="./../../assets/time0.png" v-if="userInfo.beans === 0" alt="" />
        <img src="./../../assets/time1.png" v-if="userInfo.beans === 1" alt="" />
        <img src="./../../assets/time2.png" v-if="userInfo.beans === 2" alt="" />
        <img src="./../../assets/time3.png" v-if="userInfo.beans === 3" alt="" />
      </div>
      <div class="pgree">
        <van-progress pivot-color="#ffffff" 
        text-color="#612a06" :percentage="countnum" stroke-width="8" />
      </div>
      <div class="time">{{ formatTime(totalTime / 1000) }}</div>
      <div class="tix">
        <p>集齐：三颗咖啡豆，请前往线下活动处兑换</p>
      </div>
      <div class="back">
        <div @click="toggleCountState">{{ isCountTime ? '暂停计时' : '开始计时' }}</div>
      </div>
      <!-- <div class="back">
        <div @click="toggleVisible">{{ isVisible ? '模拟息屏' : '模拟开屏' }}</div>
      </div> -->
      <div class="success">
        <van-popup v-model="dialogValue">
          <div class="dialog_title">
            <span>「成功提示」</span>
          </div>
          <div
            class="dialog_tis"
            style="font-size: 0.5333rem; margin-top: 1.3067rem"
          >
            恭喜您!
          </div>
          <div class="dialog_tis">今日的专注时间挑战已完成！</div>
          <div class="dialog_tis">获得三颗咖啡豆,收集后，前往兑换处，</div>
          <div class="dialog_tis">兑换咖啡券吧！</div>
          <div class="dialog_back">
            <div @click="dialogValue = false">确定</div>
          </div>
        </van-popup>
      </div>
      <div class="dhsuccess">
        <van-popup v-model="dhsuccessdialog">
          <div class="dialog_title">
            <span>「兑换提示」</span>
          </div>
          <div class="word">
            <div class="word_left">
              <p>亲爱的</p>
              <p>{{this.name}}</p>
              <p>同学</p>
            </div>
            <div class="word_right">
              <p>您拥有</p>
              <p>3</p>
              <p>颗咖啡豆，可兑换元醇香滑一罐！</p>
            </div>
            <h1>是否兑换？</h1>
          </div>
          <div class="tis">
            <p>请到活动现场处确认,凭借兑换成功截图才可领奖，否则无效</p>
          </div>
          <div class="dialog_btn">
            <div class="dialog_back">
              <div @click="hcback">取消</div>
              <!-- <div @click="a">取消</div> -->
            </div>
            <div class="dialog_back">
              <div @click="coffeesynthesis">确定</div>
            </div>
          </div>
        </van-popup>
      </div>
      <div class="timedialog">
        <van-popup v-model="timedialog">
          <div class="dialog_title">
            <span>「剩余提示」</span>
          </div>
          <div class="word">
            <div class="word_left">
              <p>今天的专注小工具有效时间</p>
            </div>
            <div class="word_right">
              <p>还剩</p>
              <p>{{ minute }}</p>
              <p>分钟</p>
            </div>
          </div>

          <div class="dialog_back">
            <div @click="timedialog = false">确定</div>
          </div>
        </van-popup>
      </div>
      <div class="lastdialog">
        <van-popup v-model="lastdialog">
          <div class="lastdialog_name">
            <p>微信昵称:</p>
            <p>{{this.name}}</p>
          </div>
          <div class="dialog_back">
            <div @click="lastdialog = false">确定</div>
          </div>
        </van-popup>
      </div>
    </div>
  </div>
</template>
    
    <script>
import { getCoffData, GetCoffeeData, getCoffeeSynthesis,GetTimeData } from "@/api/api";
import { formatSeconds } from "@/utils";
import { mapState, mapMutations } from "vuex";
import Music from "@/components/Music/index.vue";
export default {
  components: { Music },
  data() {
    return {
      userInfo: {
        conversion_num: 0,
        beans: 0,
      },
      gameId: "",
      gameValue: "",
      dialogValue: false,
      dhsuccessdialog: false,
      timedialog: false,
      // 兑换成功
      lastdialog: false,
      // 时间
      count: 0,
      // 进度条
      // countnum: 0,
      timer: null,
      // 判断第几次请求
      dialogIndex: 0,
      //还剩多少分钟
      // minute: 0,
      // 记录锁屏事件
      outTime: "",
      name:'',
      // 当前开始时间
      curStartTime: null,
      // 记录帧时间
      keyTime: 0,
      // 总时间 毫秒
      totalTime: 0,
      // 正在计时
      isCountTime: false,
      // 锁屏时间
      lockTime: null,
      // 锁屏时计时状态
      lockIsCountTime: false,
      // 咖啡豆增加个数，初始为1, 息屏后开启计算需要增长的个数
      addNum: 1,
      // 模拟息屏操作
      isVisible: true,
      // 显示合成
      timedata:''
    };
  },
  comments: {
    ...mapState(["outTime"]),
  },
  computed: {
    // 进度根据当前计时，计算
    countnum() {
      return Math.floor(this.totalTime / 108000)
    },
    // 剩余时间
    minute() {
      return Math.ceil((10800000 - this.totalTime) / 60000)
    }
  },
  mounted() {
    let username = JSON.parse(window.sessionStorage.getItem("userInfo"))
    this.name = username.nickname
    console.log(this.name);
    // console.log(window.sessionStorage.getItem("userInfo"));
    window.localStorage.clear();
    this.getInfo();
    document.addEventListener("visibilitychange", this.handleVisiable);
  },
  destroyed() {
    document.removeEventListener("visibilitychange", this.handleVisiable);
  },
  methods: {
    ...mapMutations(["TimeChange"]),
     // 获取今日周几
     getTime(){
      GetTimeData().then(res=>{
        console.log(res);
        if(res.data.status == 200){
          // this.timedata = res.data.data.week
        }
      })
    },
    handleVisiable(e) {
      console.log(e);
      switch (e.target.visibilityState) {
        case "prerender":
          console.log("网页预渲染，内容不可见");
          break;
        case "hidden":
          // console.log(window.localStorage.getItem("outTime"));
          // this.getCode(window.localStorage.getItem("outTime"));
          // alert(1);
          this.hiddenHandle();
          break;
        case "visible":
          this.visibleHandle();
          break;
      }
    },
    formatTime(sconds) {
      return formatSeconds(sconds);
    },
    //返回上一页
    iconback() {
      this.$router.go(-1);
    },
    // 合成取消
    hcback() {
      this.dhsuccessdialog = false;
    },
    // 获取数据
    getInfo() {
      getCoffData().then((res) => {
        console.log(res);
        if (res.data.status == 200) {
          this.userInfo.conversion_num = res.data.data.conversion_num;
          this.userInfo.beans = res.data.data.beans;
          // 初始时间赋值
          this.totalTime = this.userInfo.beans * 3600000
          // console.log(this.totalTime);
          // this.totalTime = 7190000
          // this.totalTime = 3590000
          // this.totalTime = 10790000


        }
      });
    },
    toggleVisible() {
      console.log('模拟息屏操作');
      this.isVisible = !this.isVisible
      if (!this.isVisible) {
        this.hiddenHandle()
      } else {
        this.visibleHandle()
      }
    },
    // 息屏处理
    hiddenHandle() {
      // 已经是停止状态，不执行下边的操作
      this.lockIsCountTime = this.isCountTime
      if (!this.isCountTime) return;
      // 停止计时
      this.isCountTime = false
      this.lockTime = new Date();
      console.log("内容不可见，处理后台、最小化、锁屏状态");
    },
    // 开屏处理
    visibleHandle() {
      
      // 如果锁屏是就是停止状态，不执行以下操作
      if (!this.lockIsCountTime) return
      // 开始计时
      let lockCount = new Date().getTime() - this.lockTime.getTime();
      // lockCount = 7500000
      console.log('锁屏时间', lockCount);
      this.totalTime += lockCount;
      // 加完息屏时间后，判断是否需要一次性加多颗咖啡豆
      if (this.totalTime > 7200000) {
        // 如果总时间超过2小时，判断当前咖啡豆数量，为1时正常走计时流程，为0时，加2颗咖啡豆
        if (this.userInfo.beans === 0) {
          this.userInfo.beans += 1
          this.addNum = 2
        }
      }
      if (this.totalTime > 10800000) {
        // 逻辑同上
        if (this.userInfo.beans === 1) {
          this.userInfo.beans += 1
          this.addNum = 2
        }
        if (this.userInfo.beans === 2) {
          this.userInfo.beans += 2
          this.addNum = 3
        }
      }
      this.isCountTime = true
      this.startCountTime();
      // console.log(window.localStorage.getItem("outTime"));
      // this.getCode(window.localStorage.getItem("outTime"));
      console.log("处于正常打开");
    },
    // 切换计时状态
    toggleCountState() {
      console.log(this.isCountTime);
      this.isCountTime = !this.isCountTime
      if (this.isCountTime) {
        this.startCountTime();
      } else {
        // this.stopCountTime();
      }
    },
    // 开始当前阶段计时，息屏后重启屏幕开始计时
    startCountTime() {
      console.log('开始计时', this.isCountTime);
      this.curStartTime = new Date()
      console.log(this.curStartTime);
      this.keyTime = new Date().getTime();
      this.countTime()
    },
    // 计时器，处理动态计时
    countTime() {
      if (!this.isCountTime) return
      // let countTime = new Date().getTime() - this.curStartTime.getTime()
      let curTime = new Date().getTime()
      let deltaTime = curTime - this.keyTime
      this.keyTime = curTime
      this.totalTime += deltaTime
      // 计时到指定时间，增加咖啡豆
      if (this.totalTime > 3600000) {
        if (this.userInfo.beans === 0) {
          this.userInfo.beans += 1
          this.addBeans()
        }
      }
      if (this.totalTime > 7200000) {
        if (this.userInfo.beans === 1) {
          this.userInfo.beans += 1
          this.addBeans()
        }
      }
      if (this.totalTime > 10800000) {
        // 终止计时
        this.isCountTime = false
        this.totalTime = 10800000
        if (this.userInfo.beans === 2) {
          this.userInfo.beans += 1
          this.addBeans()
        }
      }
      requestAnimationFrame(this.countTime)
    },
    // 发送生成咖啡豆请求
    addBeans() {
      console.log('添加xxx颗', this.addNum)
      // this.addNum = 3
      // this.timedialog = true;
      // return
      GetCoffeeData({
        beans: this.addNum
      }).then((res) => {
        console.log(res);
        if (res.data.status == 200) {
          this.addNum = 1;
          this.timedialog = true;
          // this.dialogIndex++;
          // if (this.dialogIndex == 1) {
          //   this.minute = 60;
          // }
          // if (this.dialogIndex == 2) {
          //   this.minute = 120;
          // }
        } 
        else {
          // alert('shibai')
          // this.userInfo.beans -= this.addNum
          // this.isCountTime = false
          this.Toast(res.data.msg || '生成咖啡豆失败，请重试')
        }
      });
    },
    // 息屏或者暂停时结束计时，当前阶段的计时加到总时间上
    stopCountTime() {
      // let countTime = new Date().getTime() - this.curStartTime.getTime()
      // this.totalTime += countTime
    },
    //开始计时
    // getCode(val) {
    //   let TIME_COUNT = null;
    //   console.log(window.localStorage.getItem("outTime"));
    //   if (window.localStorage.getItem("outTime") !== null) {
    //     TIME_COUNT = (val*1) + (window.localStorage.getItem("outTime")*1);
    //     console.log(TIME_COUNT);
    //   } else {
    //     TIME_COUNT = val;
    //     console.log(TIME_COUNT);
    //   }
    //   console.log(val);
    //   console.log();
    //   if (!this.timer) {
    //   console.log(TIME_COUNT);
    //   this.count = TIME_COUNT;
    //   this.show = false;
    //   this.timer = setInterval(() => {
    //     console.log(this.count);

    //     if (this.count < 10800) {
    //       this.count++;
    //       if (this.count % 108 == 0 && this.count !== TIME_COUNT) {
    //         this.countnum++;
    //         if (this.countnum == 99) {
    //           setTimeout(() => {
    //             this.countnum += 1;
    //           }, 1000);
    //         }
    //       }
    //       if (this.count == 7200 || this.count == 3600 || this.count == 0) {
    //         this.GetCoffeeData().then((res) => {
    //           console.log(res);
    //           if (res.data.status == 200) {
    //             this.timedialog = true;
    //             this.dialogIndex++;
    //             if (this.dialogIndex == 1) {
    //               this.minute = 60;
    //             }
    //             if (this.dialogIndex == 2) {
    //               this.minute = 120;
    //             }
    //           }
    //         });
    //       }
    //       if (this.count == 10800) {
    //         this.dialogValue = true;
    //       }
    //     } else {
    //       this.show = true;
    //       clearInterval(this.timer);
    //       this.timer = null;
    //     }

    //     console.log(this.count);
    //     window.localStorage.setItem("outTime", this.count);
    //     // this.TimeChange(this.count)
    //   }, 1000);
    //   }
    // },
    // 合成确定按钮
    coffeesynthesis() {
      this.dhsuccessdialog = false;
      getCoffeeSynthesis().then((res) => {
        console.log(res);
        if (res.data.status == 10003) {
          this.Toast(res.data.msg);
        }
        if (res.data.status == 10007) {
          this.Toast(res.data.msg);
        }
        if (res.data.status == 10008) {
          this.Toast(res.data.msg);
        }
        if (res.data.status == 15000) {
          this.Toast(res.data.msg);
        }
        if (res.data.status == 200) {
          this.lastdialog = true;
          this.Toast(res.data.msg);
          this.userInfo.conversion_num = res.data.data.conversion_num;
          this.userInfo.beans = res.data.data.beans;
          this.totalTime = this.userInfo.beans * 3600000
        }
      });
    },
    // 合成dilog显示
    hcbtn() {
      this.dhsuccessdialog = true;
    },

    // 返回
    back() {
      this.$router.push("/pagetwo");
    },
    //上传触发事件
    // afterRead(file) {
    //   // 此时可以自行将文件上传至服务器
    //   console.log(file);
    // },
    //倒计时
    goto() {
      this.getCode();
      this.timedialog = true;
    },
  },
};
</script>
    
